.contentBlock {
    flex-grow: 1;
}

.theme0, .cbetar2themes .cbeta {
    --color: black;
    --background: #d2e3d7;
    --ion-background-color: #d2e3d7;
}

.theme1, .cbetar2themes .dark {
    --color: var(--ion-text-color);
    --background: var(--ion-background-color);
    --ion-background-color: #000000;
    --ion-background-color-rgb: var(--ion-background-color);
    --ion-text-color: #e0e0e0;
    --ion-text-color-rgb: 224, 224, 224;
    --ion-color-step-50: #0d0d0d;
    --ion-color-step-100: #1a1a1a;
    --ion-color-step-150: #262626;
    --ion-color-step-200: #333333;
    --ion-color-step-250: #404040;
    --ion-color-step-300: #4d4d4d;
    --ion-color-step-350: #595959;
    --ion-color-step-400: #666666;
    --ion-color-step-450: #737373;
    --ion-color-step-500: #808080;
    --ion-color-step-550: #8c8c8c;
    --ion-color-step-600: #999999;
    --ion-color-step-650: #a6a6a6;
    --ion-color-step-700: #b3b3b3;
    --ion-color-step-750: #bfbfbf;
    --ion-color-step-800: #cccccc;
    --ion-color-step-850: #d9d9d9;
    --ion-color-step-900: #e6e6e6;
    --ion-color-step-950: #f2f2f2;
    --ion-toolbar-background: var(--ion-background-color);
    --ion-item-background: var(--ion-background-color);
    --ion-item-border-color: var(--ion-text-color);
    --ion-card-background: #1c1c1d;
}

.theme2, .cbetar2themes .light {
    --color: black;
    --background: white;
    --ion-background-color: white;
}

.theme3, .cbetar2themes .oldPaper {
    --color: black;
    --background: #f2eecb;
    --ion-background-color: #f2eecb;
}

.theme4, .cbetar2themes .marble {
    --color: black;
    --background: #b7b2af;
    --ion-background-color: #b7b2af;
}
